<template>
  <div>
    <vs-navbar :color="theme"  :text-color="theme_text" v-model="activeItem" class="menubar">
      <vs-button @click="active=!active" class="text-white" type="flat" radius="50%" icon="menu"></vs-button>

      <!-- Sidebar Menu -->
      <vs-sidebar parent="body" default-index="1"  color="primary" class="sidebarx" spacer v-model="active">

      <div class="header-sidebar" slot="header">
        <vs-avatar size="70px" :src="require(`@/assets/adeniran.jpg`)"/>

        <h4>
          Adeniran Opeyemi
          <!-- <vs-button color="primary" icon="more_horiz" type="flat"></vs-button> -->
        </h4>

      </div>

      <vs-sidebar-item index="1" icon="bookmark">
        Label detection
      </vs-sidebar-item>

      <vs-sidebar-item index="2" icon="bookmark">
        Web detection
      </vs-sidebar-item>

      <vs-sidebar-item index="3" icon="bookmark">
        Optical character recognition
      </vs-sidebar-item>

      <vs-sidebar-item index="4" icon="bookmark">
        Landmark detection
      </vs-sidebar-item>

      <vs-sidebar-item index="5" icon="bookmark">
        Image attributes
      </vs-sidebar-item>

  
      <div class="footer-sidebar" slot="footer">
        <vs-button icon="reply" color="danger" type="flat">log out</vs-button>
        <vs-button icon="settings" color="primary" type="border"></vs-button>
      </div>

    </vs-sidebar>


      <!-- Sidebar Menu -->

      <vs-navbar-title class="text-white">
        Tha AI
      </vs-navbar-title>

      <vs-spacer></vs-spacer>

      <vs-navbar-item index="0" class="text-white">
        <a href="#">
          <!-- <vs-icon icon="more_vert"></vs-icon> -->

          <vs-dropdown vs-trigger-click>
            <a class="a-icon text-white" href="#">
              <vs-icon icon="more_vert"></vs-icon>
            </a>

            <vs-dropdown-menu class="dropmenu">
              <vs-list>
                <vs-list-item title="Welcome to Tha AI" subtitle="by Horpey"></vs-list-item>
                <vs-list-item title="Dark Mode"></vs-list-item>
                <vs-list-item subtitle="© thaAI 2018"></vs-list-item>
              </vs-list>
            </vs-dropdown-menu>
          </vs-dropdown>


        </a>
      </vs-navbar-item>
    </vs-navbar>
  </div>
</template>
<script>
export default {
  name: 'Menubar',
  data:()=>({
    activeItem: 0,
    active:false,
    theme: '#4285f4',
    theme_text: "#FFFFFF",
    // profileImage: "./assets/background.png"
  })
}
</script>
<style>
.text-white{
  color: white!important;
}
.dropmenu{
  width: 200px;
}

.header-sidebar {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 100%;
}

.header-sidebar h4 {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
}

.header-sidebar h4 > button {
  margin-left: 10px;
}

.footer-sidebar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}

.footer-sidebar > button {
  border: 0px solid rgba(0,0,0,0) !important;
  border-left: 1px solid rgba(0,0,0,0.07) !important;
  border-radius: 0px !important;
}
</style>


