<template>
  <div class="theme-bg">
    <vs-row vs-type="flex" vs-justify="center" vs-align="center" vs-w="10">
      <vs-col>
        <vs-card>
          <div slot="header">
            <h3>
              Hello, I'm Tha AI !
            </h3>
          </div>
          <div>
            <span>I easily detect broad sets of objects in your images, from flowers, animals, or transportation to thousands of other object categories commonly found within images. Simply Upload an Image and I tell you more about it.</span>
          </div>
        </vs-card>
      </vs-col>
    </vs-row>

    <vs-row vs-justify="flex-end" vs-align="flex-end" vs-w="10" class="floatRight">
      <vs-col>
        <vs-card>
          <div>
            <span>Cool...</span>
          </div>
        </vs-card>
      </vs-col>
    </vs-row>

    <vs-row vs-type="flex" vs-justify="flex-end" vs-align="flex-end" vs-w="10">
      <vs-col>
        <vs-card>
          <div>
            <span>Lol, but we are still working on it 😎...</span>
          </div>
        </vs-card>
      </vs-col>
    </vs-row>

  </div>
</template>

<script>
import Menubar from './Menubar';
export default {
  name: 'message',
  components: {
    Menubar: Menubar
  },
  data () {
    return {
      msg: 'Welcome to Your Vue.js PWA'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.theme-bg{
    background-image: url('../assets/background.png');
    background-size: cover;
    background-position: center;
    padding: 16px 10px;
    height: 80vh;
}
.floatRight{
      float: right;
}
</style>



