<template>
  <div class="hello">
    <Menubar/>
    <Message/>
  </div>
</template>

<script>
import Menubar from './Menubar';
import Message from './Message';

export default {
  name: 'chatview',
  components: {
    Menubar: Menubar,
    Message: Message
  },
  data () {
    return {
      msg: 'Welcome to Your Vue.js PWA'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.examplex {
  display: flex;
  align-items: center;
  justify-content: center;
}

.examplex .a-icon {
  outline: none;
  text-decoration: none !important;
  display: flex;
  align-items: center;
  justify-content: center;
}

.examplex .a-icon i {
  font-size: 18px;
}

.myNavbar {
  color: #fff;
}
</style>



